import React, { Component } from 'react';
import { Link } from "react-router-dom";
class InnerHeader extends Component {
  render() {
    return (
      <>
      <ipl-progress-indicator style={{display:'none'}} ><div className="indicator-container"><div className="first-indicator"></div><div className="second-indicator"></div></div> <div className="tip-wrapper"><div className="logo-frame">
        <div className="row">
    <div className="col">
      <div className="panelprogress scale-transition ">
          <img src="https://lh3.googleusercontent.com/-2gKs8y2mQ3k/AAAAAAAAAAI/AAAAAAAAAgs/rSNK9GTVy80/s150/photo.jpg" className="circle responsive-img pulse z-depth-3"/>
        <span className="black-text">Sagar Mistry</span>
      </div>
    </div>
  </div>
        </div></div> </ipl-progress-indicator>
	<div id="modal1" className="modal">
    <div className="modal-content">
      <h4>Download Image</h4>
		<div className="sizebtn" >
			<a className="waves-effect waves-light btn blue darken-4" download data-value="512" target="_blank">512px</a>
			<a className="waves-effect waves-light btn blue darken-4" download data-value="256"  target="_blank">256px</a>
			<a className="waves-effect waves-light btn blue darken-4" download data-value="128" target="_blank">128px</a>
			<a className="waves-effect waves-light btn blue darken-4" download data-value="64" target="_blank">64px</a>
			<a className="waves-effect waves-light btn blue darken-3"  download data-value="32" target="_blank">32px</a>
			<a className="waves-effect waves-light btn blue darken-2" download data-value="24" target="_blank">24px</a>
			<a className="waves-effect waves-light btn blue darken-1" download data-value="16" target="_blank">16px</a>
              <div className="row">
                <div className="col s12 customsize">
                    <label>Custom Size:</label>
                  <input placeholder="XXXX" id="sizepx" type="number" min="1" max="5" maxLength="4"/>
                   <a className="waves-effect waves-light btn blue darken-4" download data-value="" target="_blank">XXXpx</a>
                </div>
              </div>
		</div>
    </div>
  </div>
  </>

    );
  }
}

export default InnerHeader;